import {CollapsibleList, ListItem, ListItemMeta} from '@rmwc/list'
import {TextField} from '@rmwc/textfield'
import {Icon} from '@rmwc/icon'


import {Typography} from '@rmwc/typography'
import {Checkbox} from '@rmwc/checkbox'
import mdDateTimePicker from 'md-date-time-picker'
import '@rmwc/list/styles';
import '@rmwc/checkbox/styles';

import '@rmwc/textfield/styles';
import {Fab} from '@rmwc/fab'
import {useState, createRef, useEffect} from 'react'
import {Dialog, DialogTitle, DialogContent, DialogButton, DialogActions, SimpleDialog} from '@rmwc/dialog'
import '@rmwc/dialog/styles';
import '@rmwc/fab/styles';
import '@rmwc/typography/styles';
import '@rmwc/icon/styles';
import Nextline from './Nextline'
import moment from 'moment'
import ClickNHold from 'react-click-n-hold'; 

function Todos({setMax, _index, setIndex}){
  const weekYear = function(a){
    return a.year() * 100 + a.week()
  }
  const dayYear = function(a){
    return a.year() * 1000 + a.dayOfYear()
  }
    var input = createRef()
    var inputName = createRef()
    var dialog = new mdDateTimePicker({"type": "date", "past": moment(), "future": moment().add(3, 'years')})
    var time = dialog.time
    const [open, setOpen] = useState(false)
    const weekNow = weekYear(moment())
    const [confirmOpen, setConfirmOpen] = useState(false)
    const [tasks, setTasks] = useState([]);
    const [currentID, setCurrentID] = useState('')
    

    useEffect(() => {
      const storedTasks = JSON.parse(localStorage.getItem('tasks'))
      if(storedTasks) setTasks(storedTasks)
    }, [])

    useEffect(() => {
      localStorage.setItem('tasks', JSON.stringify(tasks))
    }, [tasks])

    const filtered = tasks.filter(a =>　weekNow <= weekYear(moment(a.date)))


    const sorted = filtered.sort((a, b) => dayYear(moment(a.date)) - dayYear(moment(b.date)))

    var arrangedByWeek = [], currentWeek = 0, index = -1;

    for(var i = 0; i < sorted.length; i++){
      if(weekYear(moment(sorted[i].date)) > currentWeek){
        currentWeek = weekYear(moment(sorted[i].date));
        index ++;
        arrangedByWeek[index] = [];
      }

      arrangedByWeek[index].push(sorted[i])
      
    }

    console.log(arrangedByWeek, 'week')

    var arrangedByDay = arrangedByWeek.map(a => {
      var arrangedByDay = [], currentDay = 0, index = -1;

      for(var i = 0; i < a.length; i++){
        if(dayYear(moment(a[i].date)) > currentDay){
          currentDay = dayYear(moment(a[i].date));
          index ++;
          arrangedByDay[index] = [];
        }

        arrangedByDay[index].push(a[i])
      
      }

      return arrangedByDay
    })

    console.log(arrangedByDay)
    setMax(arrangedByDay.length - 1 < 0 ? 0 : arrangedByDay.length - 1)

    function handleCzech(date){

      const newTasks = [...tasks]
      const todo = newTasks.find(a => a.date === date)
      todo.checked = !todo.checked
      setTasks(newTasks)

      
    }

    function handleConfirm(){
      setConfirmOpen(true);
    }

    function handleAddTodo(){
      var newTask = 
      {
        date: dialog.time.format(),
        checked: false,
        name: inputName.current.querySelector('input').value
      }

      input.current.querySelector('input').value = inputName.current.querySelector('input').value = ''
      setTasks([...tasks, newTask])
    }

      
    

    
    
    return(
        <>
      <div class="list">
        { tasks.length === 0? null: arrangedByDay[_index].map(days => {
            return (
              <CollapsibleList
                nonInteractive
                defaultOpen={true}
                handle={
                  <ListItem>
                    <Typography use="subtitle2">
                      { moment(days[0].date).week() === weekNow ? 
                        moment(days[0].date).format('ddd') : 
                        `${moment(days[0].date).format('M')}/${moment(days[0].date).format('D')} (${moment(days[0].date).format('ddd')})`
                      }
                      </Typography>

                  </ListItem>
                }
              >
                {
                  days.map(task => {
                    return (
                      <ClickNHold onClickNHold={() => {setCurrentID(task.date); setConfirmOpen(true);  }} time={0.5}>
                      <ListItem >
                        {task.name}
                        <ListItemMeta>
                          <Checkbox checked={task.checked} onChange={() => handleCzech(task.date)}  />
                        </ListItemMeta>
                      </ListItem>
                      </ClickNHold>
                    )
                  })
                }
              </CollapsibleList>
            )
          })
          
        }
      </div>
  <Dialog
        open={open}
        onClose={evt => {
          setOpen(false);
        }}
      >
        <DialogTitle>Add Todo</DialogTitle>
        <DialogContent>
        <TextField label="Name" ref={inputName} onFocus={() => inputName.current.querySelector('.mdc-floating-label').classList.add('mdc-floating-label--float-above')}/>
        <Nextline />
        <TextField trailingIcon={{
      icon: 'event',
      onClick: (e) => {
        e.preventDefault()
        
                document.querySelector('.mddtp-picker__subtitle').classList.remove('mddtp-picker__color--active');
                document.querySelector('.mddtp-picker__title').classList.add('mddtp-picker__color--active')
                

          
          dialog.toggle(); 
          document.querySelector('#mddtp-date__ok').addEventListener('click', () => {
              input.current.querySelector('input').value = dialog.time.format("ddd[,] MMM D")
              input.current.querySelector('.mdc-floating-label').classList.add('mdc-floating-label--float-above')
            })
        }
    }} ref={input} label="Date" readOnly />
    
    
        </DialogContent>
        <DialogActions>
          <DialogButton action="close">Cancel</DialogButton>
          <DialogButton action="accept" isDefaultAction onClick={() => handleAddTodo()}>
            Add
          </DialogButton>
        </DialogActions>
      </Dialog>
      
      <Fab raised icon="add" onClick={() => {
        setOpen(true);
        input.current.querySelector('.mdc-floating-label').classList.remove('mdc-floating-label--float-above')
        inputName.current.querySelector('.mdc-floating-label').classList.remove('mdc-floating-label--float-above')
      }

      
      
      
        
        } 
      />
      <SimpleDialog open={confirmOpen} 
      onClose={(evt) => (evt.detail.action === 'accept' && setTasks(a => a.filter(b => b.date !== currentID)))} 
      onClosed={() => setConfirmOpen(false)} 
      body={<span>Do you want to delete the task?</span>} 
      title={<span>Confirm</span>}
      acceptLabel={'Yes'}/>
      
    </>
  )
}
export default Todos